@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400&display=swap);
body {
  margin: 0;
  font-family: 'EliceDigitalBaeum_Regular', 'Nunito', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #dae0e9;
}

code {
  font-family: 'EliceDigitalBaeum_Regular', 'Nunito', source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'EliceDigitalBaeum_Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/EliceDigitalBaeum_Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  unicode-range: U+1100-11FF, U+3130-318F, U+A960-A97F, U+AC00-D7A3, U+D7B0-D7FF;
}

.form {
  display: flex;
}

.form input {
  flex: 1 1;
  font-size: 1.25rem;
  outline: none;
  border: none;
  border-bottom: 1px solid #758eac;
}

.create-button {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: 1rem;
  background: #758eac;
  border-radius: 3px;
  color: white;
  font-weight: 600;
  cursor: pointer;
}

.create-button:hover {
  background: #a3b2c9;
}
.palette {
  display: flex;
  justify-content: center;
}

.color {
  width: 2.5rem;
  height: 2.5rem;
  opacity: 0.5;
  transition: all 0.2s;
  cursor: pointer;
}

.color:hover, .plus:hover, .color.active, .color:active {
  opacity: 1;
}

.color + .color {
  margin-left: 2rem;
}

.plus {
  width: 2.4rem;
  height: 2.4rem;
  margin-left: 2rem;
  opacity: 0.5;
  transition: all 0.2s;
  cursor: pointer;
  border: 0.1rem solid gray;
  border-style: dashed;
  color: gray;
  text-align: center;
  line-height: 2.5rem;
  font-size: 1.5rem;
}

