.todo-item {
  padding: 1.5rem;
  font-size: 1rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: all 0.15s;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.todo-item:hover {
  background: #eff5fc;
}

.todo-item:hover .remove, .todo-item:hover .more {
  opacity: 1;
}

.todo-item + .todo-item {
  border-top: 1px solid #f1f3f5;
}


.remove {
  margin-right: 1rem;
  color: #ff3333;
  font-weight: 600;
  opacity: 0;
}

.more {
  margin: 0 1rem;
  color: #2e5583;
  font-weight: 600;
  opacity: 0;
}

.todo-text {
  flex: 1 1;
  word-break: break-all;
}

.checked {
  text-decoration: line-through;
  color: #adb5bd;
}

.check-mark {
  font-size: 1rem;
  /* line-height: 1rem; */
  margin: 0 0 0 1rem;
  color: #758eac;
  font-weight: 500;
}

.todo-template {
  background: white;
  width: 512px;
  min-height: 40rem;
  box-shadow: 5px 5px 10px rgba(0,0,0,0.16); /* 그림자 */ 
  margin: 0 auto;
  margin-top: 4rem;
  border-radius: 10px;
  overflow: hidden;
}

.user-info {
  position: fixed;
  margin: 0 auto;
  margin-top: 1rem;
  /* text-align: right; */
  /* justify-self: right; */
  right: calc(50% - 256px + 1.5rem);
  font-size: 1.5rem;
  color: white;
}

.title {
  padding: 2rem;
  font-size: 2.5rem;
  text-align: center;
  font-weight: 500;
  background: #2e5583;;
  color: white;
}

.form-wrapper {
  padding: 1rem;
  border-bottom: 1px solid #2e5583;
}

.todos-wrapper {
  min-height: 5rem;
}

.palette-wrapper {
  padding: 2rem;
  background: #f2f3f5;
}

.detail {
  background: white;
  width: 512px;
  min-height: 40rem;
  box-shadow: 5px 5px 10px rgba(0,0,0,0.16); /* 그림자 */ 
  margin: 0 auto;
  margin-top: 4rem;
  border-radius: 10px;
  overflow: hidden;
}

.detail-back {
  position: fixed;
  margin: 0.9rem 1.3rem;
  font-size: 1.85rem;
  color: white;
}

.detail-title {
  padding: 1rem;
  font-size: 1.5rem;
  text-align: center;
  font-weight: 500;
  background: #2e5583;;
  color: white;
}

.detail-content {
  margin: 1rem;
}

.detail-textarea {
  margin: 0 auto;
  text-align: center;
  justify-content: center;
}

.detail-textarea textarea {
  font-family: 'EliceDigitalBaeum_Regular';
  min-width: 450px;
  min-height: 29rem;
  margin: 0 auto;
  padding: 10px;
  resize: none;
  border: none;
  outline: none;
  border: 0.01rem solid #eeeeee;
  border-radius: 5px;
}

.detail-buttons {
  margin: 0.8rem;
  text-align: right;
}

.detail-buttons button {
  width: 3.5rem;
  height: 2rem;
  margin-left: 1rem;
  border: 1px solid #758eac;
  border-radius: 0.5rem;
  background: #758eac;
  color: white;
  font-size: 0.9rem;
  font-family: 'EliceDigitalBaeum_Regular';
}

.detail-buttons button:hover {
  cursor: pointer;
  background: #a3b2c9;
  border-color: #a3b2c9;
}

.user {
  background: white;
  width: 512px;
  min-height: 40rem;
  box-shadow: 5px 5px 10px rgba(0,0,0,0.16); /* 그림자 */ 
  margin: 0 auto;
  margin-top: 4rem;
  border-radius: 10px;
  overflow: hidden;
}

.user-back {
  position: fixed;
  margin: 0.9rem 1.3rem;
  font-size: 1.85rem;
  color: white;
}

.user-title {
  padding: 2rem;
  font-size: 2.5rem;
  text-align: center;
  font-weight: 500;
  background: #2e5583;;
  color: white;
}

.user-sub-title {
  font-size: 1.1rem;
  font-weight: 600;
  margin-left: 0.5rem;
}

/* .user-sub-title button {
  line-height: 1.2rem;
  font-size: 0.8rem;
} */

.user-content {
  min-height: 10rem;
  margin: 1rem;
}

.user-profile, .user-setting {
  padding: 1rem;
}

.user-item {
  margin: 1rem 0 0 0;
  padding: 1rem;
  border-bottom: 1px solid #f1f3f5;
}

.user-buttons {
  position: fixed;
  top: 7.5rem;
  right: calc((100% - 512px)/2 + 1rem);
  display: inline;
}

button {
  /* min-width: 3rem; */
  min-height: 1.5rem;
  margin-left: 1rem;
  /* line-height: 1rem; */
  border: 1px solid #758eac;
  border-radius: 0.5rem;
  background: #758eac;
  color: white;
  font-size: 0.8rem;
  font-family: 'EliceDigitalBaeum_Regular';
}

button:hover {
  cursor: pointer;
  background: #a3b2c9;
  border-color: #a3b2c9;
}
